@import "../../template/mixin.scss";
@import "../../template/colors.scss";
.culture {
  &__container {
    @include flex-container();
    position: relative;
    padding-bottom: 253px;
  }
  &__image {
    margin-left: 20px;
  }
  &__text {
    padding-left: 42px;
    max-width: 690px;
    h2 {
      max-width: 555px;
    }
    h2,
    h3,
    p {
      margin-bottom: 28px;
    }
    p{
      font-weight: 400;
    }
  }
}
@media screen and (max-width: 1500px) {
  .culture {
    &__container {
      top: 0px;
    }
  }
}
@media screen and (max-width: 1110px) {
  .culture {
    &__container {
      flex-direction: column;
      padding-bottom: 100px;
    }
    &__image {
      margin-left: 0px;
      margin-top: 20px;
      max-width: 597px;
      width: 100%;
    }
  }
}

